<template>
    <div id="footer" class="footer">
        <p class="mt-3">{{ copyright }}</p>
    </div>
</template>

<script>
export default {
  data () {
    return {
        copyright: 'Copyright (C) Sourceright Sdn Bhd. All Rights Reserved. 2017.'
    }
  }
}
</script>

<style>
.footer {
    bottom: 0;
    margin: 0 auto;
    text-align: center;
    width: 100%;
    position:fixed;
    background:#222;
    color: white;
}
</style>



