import home from './components/Home.vue';
import reasons from './components/reason/Reasons.vue';
import newReason from './components/reason/NewReason.vue';
import editReason from './components/reason/EditReason.vue';
import projects from './components/project/Projects.vue';
import newProject from './components/project/NewProject.vue';
import editProject from './components/project/EditProject.vue';
import companies from './components/company/Companies.vue';
import newCompany from './components/company/NewCompany.vue';
import editCompany from './components/company/EditCompany.vue';
import users from './components/user/Users.vue';
import newUser from './components/user/NewUser.vue';
import editUser from './components/user/EditUser.vue';
import employees from './components/employee/Employees.vue';
import newEmployee from './components/employee/NewEmployee.vue';
import editEmployee from './components/employee/EditEmployee.vue';

export default [
    { path: '/', component: home},
    { path: '/reason', component: reasons},
    { path: '/reason/new', component: newReason},
    { path: '/reason/edit/:id', component: editReason},
    { path: '/project', component: projects},
    { path: '/project/new', component: newProject},
    { path: '/project/edit/:id', component: editProject},
    { path: '/company', component: companies},
    { path: '/company/new', component: newCompany},
    { path: '/company/edit/:id', component: editCompany},
    { path: '/user', component: users},
    { path: '/user/new', component: newUser},
    { path: '/user/edit/:id', component: editUser},
    { path: '/employee', component: employees},
    { path: '/employee/new', component: newEmployee},
    { path: '/employee/edit/:id', component: editEmployee}
]


