<template>
<div id="projects"> 
  <div class="container-fluid">  
    <div class="col-sm-12 col-sm-push-1">
        <h4>{{ title }}</h4>
        <hr>
        <div class="row">
            <div class="col-sm-12">
                <router-link to="/project/new"><img src="../../assets/images/add_record.png" style="height: 30px; margin-bottom: 5px;"></router-link>
                <a href="" style="margin-left: 20px;"><u>IMPORT</u></a>

                <a href=""><img src="../../assets/images/print.png" style="height: 30px; float: right;"></a>
                <a href=""><img src="../../assets/images/download.png" style="height: 30px; float: right;"></a>

            </div>
        </div>
        <div class="row">
            <div class="col-sm-12">
                    <v-data-table
                        :headers="headers"
                        :items="projects"
                        hide-actions
                        class="elevation-1"
                    >
                        <template slot="items" slot-scope="props">
                            <td class="justify-center layout px-0">
                                <v-icon
                                    small
                                    class="mr-2 pt-3"
                                @click="redirectTo('/project/edit/' + props.item.id)"
                                >
                                    edit
                                </v-icon>                   
                                <v-icon
                                    small
                                    class="pt-3"
                                    @click="deleteProject(props.item, props.item.id, props.item.display_name)"
                                >
                                    delete
                                </v-icon>
                            </td>
                            <td class="text-xs-left">{{ props.item.URL_short_name }}</td>
                            <td class="text-xs-left">{{ props.item.display_name }}</td>
                            <td class="text-xs-left">{{ props.item.created_at | formatDate }}</td>
                            <td class="text-xs-left">{{ props.item.ended_at | formatDate }}</td>
                            <td class="text-xs-center">{{ props.item.is_enabled | toggleIsEnabled }}</td>
                            <td class="text-xs-left">{{ props.item.remark }}</td>
                        </template>
                        <template slot="pageText" slot-scope="props">
                            Rows Per Page {{ props.pageStart }} - {{ props.pageStop }} of {{ props.itemsLength }}
                        </template>
                    </v-data-table>
            </div>
        </div>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            title: 'PROJECT LISTING',
            headers: [
                    { text: 'ACTION', sortable: false, value: 'action' },
                    { text: 'ID', value: 'URL_short_name' },
                    { text: 'PROJECT NAME', value: 'display_name' },
                    { text: 'EFFECTIVE FROM', value: 'created_at' },
                    { text: 'EFFECTIVE UNTIL', value: 'ended_at' },
                    { text: 'DISABLED', value: 'is_enabled' },
                    { text: 'REMARK', sortable: false, value: 'remark' },
            ],            
            status: '',
            projects: []
        }
    },

    methods: {
        loadProjects() {
            var vm = this

            axios.get('http://clockit.transact2.com/api/clock/projects/list')
            .then(function (response) {
                vm.projects = response.data.rec;
            })
            .catch(function (error) {
                vm.status = 'An error occured.' + error;
            })
        },

        deleteProject(item, id, name) {
            var vm = this
            const index = vm.projects.indexOf(item)

            if(confirm('Are you sure you want to delete ' + name + '?')) {
                axios.delete('http://clockit.transact2.com/api/clock/project/' + id)
                .then(function (response) {
                        vm.projects.splice(index, 1)
                    })
                .catch(function (error) {
                    alert('An error occured.')
                })
                }
            },

        redirectTo(path) {
            this.$router.push(path)
           }
    },

    created() {
        this.loadProjects();
    }
}
</script>

<style>

</style>



