<template>
<div id="users">  
  <div class="container-fluid">  
    <div class="col-sm-12 col-sm-push-1">
        <h4>{{ title }}</h4>
        <hr>
        <div class="row">
            <div class="col-sm-12">
                <router-link to="/user/new"><img src="../../assets/images/add_record.png" style="height: 30px; margin-bottom: 5px;"></router-link>
                <a href="" style="margin-left: 20px;"><u>IMPORT</u></a>

                <a href=""><img src="../../assets/images/print.png" style="height: 30px; float: right;"></a>
                <a href=""><img src="../../assets/images/download.png" style="height: 30px; float: right;"></a>

            </div>
        </div>
        <div class="row">
            <div class="col-sm-12">
                <v-data-table
                    :headers="headers"
                    :items="admins"
                    hide-actions
                    class="elevation-1"
                >
                    <template slot="items" slot-scope="props">
                        <td class="justify-center layout px-0">
                            <v-icon
                                small
                                class="mr-2 pt-3"
                                @click="redirectTo('/user/edit/' + props.item.id)"
                            >
                                edit
                            </v-icon>
                            <v-icon
                                small
                                class="pt-3"
                                @click="deleteAdminUser(props.item, props.item.id, props.item.display_name)"
                            >
                                delete
                            </v-icon>
                        </td>
                        <td class="text-xs-left">{{ props.item.display_name }}</td>
                        <td class="text-xs-left">{{ props.item.email }}</td>
                        <td class="text-xs-left">{{ props.item.effective_at | formatDate }}</td>
                        <td class="text-xs-left">{{ props.item.effective_till | formatDate }}</td>
                        <td class="text-xs-center">{{ props.item.is_enabled | toggleIsEnabled }}</td>
                    </template>
                    <template slot="pageText" slot-scope="props">
                        Rows Per Page {{ props.pageStart }} - {{ props.pageStop }} of {{ props.itemsLength }}
                    </template>
                </v-data-table>
            </div>           
        </div>
    </div>
  </div>

</div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            title: 'ADMIN USERS',
            headers: [
                    { text: 'ACTION', sortable: false, value: 'action' },
                    { text: 'FULL NAME', value: 'display_name' },
                    { text: 'EMAIL', value: 'email' },
                    { text: 'VALID FROM', value: 'effective_at' },
                    { text: 'VALID UNTIL', value: 'effective_till' },
                    { text: 'DISABLED', value: 'is_enabled' },
            ],            
            admins: []
        }
    },

    methods: {
        loadAdmins() {
            var vm = this

            axios.get(' http://clockit.transact2.com/api/admin/list')
            .then(function (response) {
                vm.admins = response.data.rec;
            })
            .catch(function (error) {
                vm.status = 'An error occured.' + error;
            })
        },

        deleteAdminUser(item, id, name) {
            var vm = this
            const index = vm.admins.indexOf(item)

            if(confirm('Are you sure you want to delete ' + name + '?')) {
                axios.delete('http://clockit.transact2.com/api/admin/' + id)
                .then(function (response) {
                    vm.admins.splice(index, 1)
                })
                .catch(function (error) {
                    alert('An error occured.')
                })
            }
        },

        redirectTo(path) {
            this.$router.push(path)
        }        
    },

    created() {
        this.loadAdmins();
    }

  
}
</script>

<style>

</style>



