<template>
    <div id="editEmployee">
        <div class="col-sm-11 col-sm-push-1">
            <h4>{{ title }}</h4>
            <hr>
            <div class="row">
                <div class="col-sm-11">
                    <form class="form" @submit.prevent="updateEmployee">
                        <div class="form-group row">
                            <div class="col-sm-2" >
                                <label class="control-label" for="full_name">{{ label.full_name }}</label>
                            </div>
                            <div class="col-sm-8">
                                <input type="text" class="form-control" required id="full_name" name="full_name" v-model="employee.employee_name" >
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="staff_id">{{ label.staff_id }}</label>
                            </div>
                            <div class="col-sm-8">
                                <input type="text" class="form-control"  id="staff_id" name="staff_id" v-model="employee.staff_id" >
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="company">{{ label.company }}</label>
                            </div>
                            <div class="col-sm-8">
                                <select class="form-control" id="company" name="company" required v-model="employee.company_id">
                                    <option v-bind:value="employee.company_id" selected>{{ employee.company_name }}</option>                
                                    <option v-for="(company, index) in companies" :key="index" v-bind:value="company.id">{{ company.display_name }}</option>
                                </select>
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="email">{{ label.email }}</label>
                            </div>
                            <div class="col-sm-8">
                                <input type="email" class="form-control" required id="email" name="email" v-model="employee.email" >
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="New IC">{{ label.new_ic }}</label>
                            </div>
                            <div class="col-sm-8">
                                <input type="text" class="form-control" id="new_id" name="new_ic" v-model="employee.card_id" >
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="created_at">{{ label.created_at }}</label>
                            </div>
                            <div class="col-sm-3">
                                <input type="date" class="form-control" id="created_at" name="created_at" required v-model="employee.created_at" >
                            </div>
                            <div class="col-sm-2">
                                <label class="control-label" for="ended_at">{{ label.ended_at }}</label>
                            </div>
                            <div class="col-sm-3">
                                <input type="date" class="form-control" id="ended_at" name="ended_at" required v-model="employee.ended_at" >
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="id_number">{{ label.id_number }}</label>
                            </div>
                            <div class="col-sm-8">
                                <input type="text" class="form-control" id="id_number" name="id_number" v-model="employee.id_number" >
                            </div>
                        </div>

                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="passport">{{ label.passport }}</label>
                            </div>
                            <div class="col-sm-8">                                
                                <table class="table table-bordered">
                                    <tr class="passport_number_table">
                                        <th>{{ label.passport_number }}</th>
                                        <th>{{ label.passport_v }}</th>
                                        <th>{{ label.passport_e }}</th>
                                    </tr>
                                    <tr>
                                        <td><input type="text" class="form-control" id="passport_number" name="passport_number" v-model="employee.passport_number" ></td>
                                        <td><input type="date" class="form-control" id="valid_from" name="valid_from" v-model="employee.passport_issued"></td>
                                        <td><input type="date" class="form-control" id="expiry_date" name="expiry_date" v-model="employee.passport_expiry"></td>
                                    </tr>
                                </table>
                            </div>
                        </div>
                        <div class="form-group row">
                            <div class="col-sm-2">
                                <label class="control-label" for="can_batch">{{ label.can_batch }}</label>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="radio" name="can_batch_options" id="batch_yes" value="1" v-model="employee.can_batch">
                                    <label class="form-check-label" for="inlineRadio1">{{ label.batch_yes }}</label>
                                </div>
                                <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="radio" name="can_batch_options" id="batch_no" value="0" v-model="employee.can_batch">
                                    <label class="form-check-label" for="inlineRadio2">{{ label.batch_no }}</label>
                                </div>
                            </div>   
                        </div>

                        <div class="form-group row text-center">
                            <div class="col-sm-11">                        
                                <button type="submit" class="btn btn-primary" >SAVE</button>
                                <router-link to="/employee"><button class="btn btn-primary">CANCEL</button></router-link>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
          title: 'EDIT STAFF',
          label: {
                full_name: 'FULL NAME',
                staff_id: 'STAFF ID',
                company: 'COMPANY',
                email: 'EMAIL',
                new_ic: 'NEW IC',
                created_at: 'ACTIVE FROM',
                ended_at: 'ACTIVE UNTIL',
                id_number: 'ID',
                passport: 'PASSPORT',
                passport_number: 'NUMBER',
                passport_v: 'VALID FROM',
                passport_e: 'EXPIRY DATE',
                can_batch: 'MANAGE BATCH CLOCKING',
                batch_yes: 'YES',
                batch_no: 'NO'
            },
            id: this.$route.params.id,
            employee: {
                employee_name: null,
                staff_id: null,
                company_id: null,
                company_name: null,
                email: null,
                card_id: null,
                created_at: null,
                ended_at: null,
                id_number: null,
                passport: null,
                passport_issued: null,
                passport_expiry: null,
                can_batch: null
            },
            companies: [],
            validationMsg: '',
            status: '',
        }
    },

    methods: {
        loadEmployee() {
            var vm = this
            var moment = require('moment')

            axios.get('http://clockit.transact2.com/api/user/'+ vm.id)
            .then(function (response) {
                var employee = response.data.rec[0]
                console.log(employee)
                vm.employee.employee_name = employee.employee_name
                vm.employee.staff_id = employee.staff_id
                vm.employee.company_id = employee.company_id
                vm.employee.company_name = employee.company_name
                vm.employee.email = employee.email
                vm.employee.card_id = employee.card_id            
                vm.employee.created_at = moment(employee.created_at).format('YYYY-MM-DD')
                vm.employee.ended_at = moment(employee.ended_at).format('YYYY-MM-DD')
                vm.employee.id_number = employee.id_number
                vm.employee.passport = employee.passport
                vm.employee.passport_issued = employee.passport_issued
                vm.employee.passport_expiry = employee.passport_expiry
                vm.employee.can_batch = employee.can_batch
            })
            .catch(function (error) {
                vm.status = 'An error occured.' + error
            })  
        },

        loadCompanies() {
            var vm = this

            axios.get('http://clockit.transact2.com/api/companies')
            .then(function (response) {
                vm.companies = response.data.rec
            })
            .catch(function (error) {
                vm.status = 'An error occured.' + error
            })
        },

        updateEmployee() {
            var vm = this

            axios.put('http://clockit.transact2.com/api/employee/'+ vm.id, {
                display_name: vm.employee.employee_name,
                staff_id: vm.employee.staff_id,
                company_id: vm.employee.company_id,
                email: vm.employee.email,
                effective_at: vm.employee.created_at,
                effective_till: vm.employee.ended_at,
                card_id: vm.employee.card_id,
                id_number: vm.employee.id_number,
                passport: vm.employee.passport,
                passport_issued: vm.employee.passport_issued,
                passport_expiry: vm.employee.passport_expiry,
                can_batch: vm.employee.can_batch
            })
            .then(function (response) {  
                console.log(response)
                vm.status = response.data.status

                if(vm.status === 'success') {
                    vm.$router.push('/employee')
                }

                if(response.data.errors.length > 0)
                {
                    vm.validationMsg = response.data.errors[0].msg
                    alert('Validation error. ' + vm.validationMsg)
                }             
            })
            .catch(function (error) {
                if(error.response) {
                    vm.status = error.response.data
                    alert('An error occured. ' + vm.status)
                }
            })

        },        
    },
    created() {
        this.loadEmployee()
        this.loadCompanies()
    }
}
</script>

<style>

</style>


