<template>
<div id="employees">
  <div class="container-fluid">  
    <div class="col-sm-12 col-sm-push-1">
        <h4>{{ title }}</h4>
        <hr>
        <div class="row">
            <div class="col-sm-12">
                <router-link to="/employee/new"><img src="../../assets/images/add_record.png" style="height: 30px; margin-bottom: 5px;"></router-link>
                <a href="" style="margin-left: 20px;"><u>IMPORT</u></a>

                <a href=""><img src="../../assets/images/print.png" style="height: 30px; float: right;"></a>
                <a href=""><img src="../../assets/images/download.png" style="height: 30px; float: right;"></a>

            </div>
        </div>
        <div class="row">
            <div class="col-sm-12">
                <v-data-table
                    :headers="headers"
                    :items="users"
                    hide-actions
                    class="elevation-1"
                >
                    <template slot="items" slot-scope="props">
                        <td class="justify-center layout px-0">
                            <v-icon
                                small
                                class="mr-2 pt-3"
                            @click="redirectTo('/employee/edit/' + props.item.id)"
                            >
                                edit
                            </v-icon>
                            <v-icon
                                small
                                class="pt-3"
                                @click="deleteEmployee(props.item, props.item.id, props.item.employee_name)"
                            >
                                delete
                            </v-icon>
                        </td>
                        <td class="text-xs-left">{{ props.item.employee_name }}</td>
                        <td class="text-xs-left">{{ props.item.company_shortname }}</td>
                        <td class="text-xs-left">{{ props.item.company_name }}</td>
                        <td class="text-xs-left">{{ props.item.email }}</td>
                        <td class="text-xs-left">{{ props.item.effective_at | formatDate }}</td>
                        <td class="text-xs-left">{{ props.item.effective_till | formatDate  }}</td>
                        <td class="text-xs-left">{{ props.item.id_number }}</td>
                        <td class="text-xs-center">{{ props.item.is_batch | toggleYN }}</td>
                        <td></td>
                    </template>
                    <template slot="pageText" slot-scope="props">
                        Rows Per Page {{ props.pageStart }} - {{ props.pageStop }} of {{ props.itemsLength }}
                    </template>
                </v-data-table>
            </div>
        </div>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
    data () {
        return {
            title: 'REGISTERED STAFF',
            headers: [
                    { text: 'ACTION' , sortable: false, value: 'action' },
                    { text: 'FULL NAME' , value: 'employee_name' },
                    { text: 'COMPANY ID' , value: 'companyId' },
                    { text: 'COMPANY NAME', value: 'companyName' },
                    { text: 'EMAIL', value: 'email' },
                    { text: 'ACTIVE FROM', value: 'activeFrom' },
                    { text: 'ACTIVE UNTIL', value: 'activeUntil' },
                    { text: 'ID' , sortable: false, value: 'id' },
                    { text: 'MANAGE BATCH CLOCKING', value: 'manageBatchClocking' },
                    { text: 'QR CODE', value: 'qrCode' },
            ],
            users: [],
            
        }
    },
    
    methods: {
        loadUsers() {
            var vm = this

            axios.get(' http://clockit.transact2.com/api/user/list')
            .then(function (response) {
                vm.users = response.data.rec
                console.log(vm.users)
            })
            .catch(function (error) {
                vm.status = 'An error occured.' + error
            })
        },

        redirectTo(path) {
            this.$router.push(path)
        },

        deleteEmployee(item, id, name) {
            var vm = this
            const index = vm.users.indexOf(item)

            if(confirm('Are you sure you want to delete ' + name + '?')) {
                axios.delete('http://clockit.transact2.com/api/employee/' +  id)
                .then(function (response) {
                    vm.users.splice(index, 1)
                })
                .catch(function (error) {
                    alert('An error occured.')
                })
            }

            //confirm('Are you sure you want to delete this item?') && this.users.splice(index, 1)
        }
    },

    created() {
        this.loadUsers();
    }
}
</script>

<style>
</style>



